.guarantee {
}
.guarantee__box {
  background: rgba(241, 241, 241, 1);
  padding-top: 30px;
  padding-bottom: 30px;
  padding-left: 20px;
  padding-right: 20px;
}
.guarantee__title {
  font-weight: 800;
  font-size: 26px;
  line-height: 1.3;

  text-align: center;
  letter-spacing: 0.03em;

  color: #111518;

  margin-bottom: 30px;
}

.guarantee__list {
  //   background: rgba(241, 241, 241, 1);
  //   padding-top: 30px;
  //   padding-bottom: 30px;
  //   padding-left: 15px;
}

.guarantee__item {
  position: relative;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.6;

  color: #3d3d3d;

  padding-left: 28px;

  margin-right: 23px;

  &:not(:last-child) {
    padding-bottom: 23px;
    border-left: 1px dashed #b9b9b9;
  }

  @media (min-width: 1280px) {
    font-size: 20px;
    line-height: 1.4;
  }
}

// .guarantee__item {
//   margin-right: 23px;
//   position: relative;
// }

.guarantee__item::before {
  content: ' ';
  display: inline-block;

  position: absolute;
  top: 0;
  left: -8px;
  width: 15px;
  height: 20px;

  margin-right: 20px;
  background-image: url(../images/svg/fire.svg);
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
}

.guaranttee__accent-text {
  color: #ff6b09;
}
